import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  Image,
  TouchableWithoutFeedback,
  TouchableOpacity,
  Linking
} from 'react-native';

import IconF from 'react-native-vector-icons/FontAwesome'


const styles = StyleSheet.create({
  title:{
    fontStyle: 'italic',
    fontSize: 22,
    color: "#777",
  },
  linkline:{
    flexDirection: "row",
    alignItems: 'center'
  },
  icon:{
    marginLeft: 10,
    color: "#777"
  }
});

class LinkLine extends Component{
  /* contains an entity and all it's links
  props.title = title of this line
  props.links = array of all links
  e.g.:
    Organizer: <Link-to-instagram>, <Link-to-google-maps>
  */
  _renderIconLink(link){
    console.log(link)
    var IgRegExp = RegExp("instagram.com")
    var FbRegExp = RegExp("facebook.com")
    var YtbRegExp = RegExp("youtube.com")
    if (IgRegExp.test(link))
      return <IconF name="instagram" onPress={() => Linking.openURL(link)} size={35} style={styles.icon}/>
    if (FbRegExp.test(link))
      return <IconF name="facebook-square" onPress={() => Linking.openURL(link)} size={35} style={styles.icon}/>
    if (YtbRegExp.test(link))
      return <IconF name="youtube-square" onPress={() => Linking.openURL(link)} size={35} style={styles.icon}/>
    else
      return <IconF name="globe" onPress={() => Linking.openURL(link)} size={35} style={styles.icon}/>
  }

  render(){
    var arr = this.props.links.map((link) => this._renderIconLink(link))
    var title = <Text style={styles.title}>{this.props.title} </Text>
    return <View style={styles.linkline}>{title}{arr}</View>
  }
}

export default class EventLinks extends Component{
  constructor(props)
  {
    super(props)
  }
  render(){
    if (this.props.links == undefined)
      return (<View></View>)
    var arr = []
    var links = this.props.links
    Object.keys(links).forEach(function(key,index) {
        var line = <LinkLine title={key} links={links[key]}/>
        arr.push(<LinkLine title={key} links={links[key]}/>)
    })
    return(
      <View>
        {arr}
      </View>
    )
  }
}
