export default class HomeScreen extends Component {
  constructor(props)
  {
    super(props)
    this.state =
    {
      events: null,
      option: 1, //0: dnes, 1: tento týždeň, 2: nasledujúce, 3: minulé
      eventstorender: [],
      dividedevents: [],
      isFetching: true
    }
  }

  fetchData(){
    fetch("https://hangouter-backend.herokuapp.com/api/v1/events")
    .then(response => response.json())
    .then((responseJson)=> {
      this.setState({
       events: this.sortEvents(responseJson)
      })
      this.setState({
        dividedevents: this.processEvents(this.state.events),
        isFetching: false
      })})
    .catch(error=>console.log(error)) //to catch the errors if any
  }

  // This should be happening on backend
  processEvents(events){
    if (!events)
      return []
    var resultEvents = {
        today: [],
        thisWeek: [],
        thisWeekend: [],
        future: [],
        past: []
    };
    events.forEach(function (item, index){
      var start = new Date(item.starttime)
      var end = (new Date(item.endtime).getFullYear() !== 2000) ? new Date(item.endtime) : null
      if (end){
        var todaystart = new Date()
        todaystart.setHours(0)
        todaystart.setMinutes(0)
        todaystart.setSeconds(0)
        var todayend = new Date(todaystart)
        todayend.setDate(todayend.getDate() + 1)
        if (HomeScreen.intervalsOverlapping(start, end, todaystart, todayend))
          resultEvents.today.push(item)
      }else{
        //set today dates
        var todaystart = new Date()
        todaystart.setHours(0)
        todaystart.setMinutes(0)
        todaystart.setSeconds(0)
        var todayend = new Date(todaystart)
        todayend.setDate(todayend.getDate() + 1)

        //check if event is already done
        if (start < todaystart){
          resultEvents.past.push(item)
          return
        }
        // check if event is today
        if (HomeScreen.inInterval(todaystart, todayend, start))
          resultEvents.today.push(item)
        //set week dates
        var day = todaystart.getDay()
        var weekstart = new Date(todaystart)
        weekstart.setDate(todaystart.getDate() - day + (day == 0 ? -6:1))
        var weekend = new Date(weekstart)
        weekend.setDate(weekend.getDate()+7)
        // check if event is this week
        if (HomeScreen.inInterval(weekstart, weekend, start))
          resultEvents.thisWeek.push(item)
        var weekendstart = new Date(weekstart)
        weekendstart.setDate(weekendstart.getDate() + 5)
        // or this weekend
        if (HomeScreen.inInterval(weekendstart, weekend, start))
          resultEvents.thisWeekend.push(item) //TODO Not future but weekend
        // or after that
        if (start > weekend)
          resultEvents.future.push(item)
      }
    })
    return resultEvents
    /*events.forEach(function (item, index) {
      var start = new Date(item.starttime)
      var end = item.endtime ? new Date(item.endtime) : null
      var today = new Date()
      if (HomeScreen.isActive(item)){
          if (end){
            start.setHours(0);
            start.setMinutes(0);
            end.setHours(0);
            end.setMinutes(0);
            if (start <= today && today <= end)
              sectiondata[1].data.push(item)
            else
              sectiondata[0].data.push(item)
          }else{
            start.setHours(0);
            start.setMinutes(0);
            if (start==today)
              sectiondata[1].data.push(item)
            else
            sectiondata[0].data.push(item)
          }
      }else{
        sectiondata[2].data.push(item)
      }
    });*/
  }
  sortEvents(events){
    return events.sort((a,b) => {
      var adate = new Date(a.starttime)
      var bdate = new Date(b.starttime)
      if (adate > bdate){
        return -1
      }else if (adate==bdate){
        return 0
      }else{
        return 1
      }
    })
    return events
  }

  componentDidMount(){
    this.fetchData()
  }

  onRefresh() {
   this.setState({ isFetching: true }, function() { this.fetchData() });
  }

  static intervalsOverlapping(a1, a2, b1, b2){
    return (a1 < b2 && a2 > b1)
  }

  static inInterval(a1, a2, x){
    return (a1 < x && x < a2)
  }

  static notStarted(event){
    return (new Date() < new Date(event.starttime))
    }

  _flatListRenderer({item}){
    var Element = withRouter(({history}) => (
      <TouchableOpacity onPress={() => {history.push("/details/" + item._id, {item: item})}}>
          <EventElement active={HomeScreen.notStarted(item)} item={item}/>
      </TouchableOpacity>))
    return ( <Element/> )
  }

  _sectionHeaderRenderer({ section: { title }}){
    return(
    <Text style={styles.sectionheader}>{title}</Text>)
  }

  _optionChanged(option)
  {
    var newevents = null
    switch (option)
    {
      // Today
      case 0:
        newevents = this.state.dividedevents.today
        break

      case 1:
        newevents = this.state.dividedevents.thisWeek
        break

      case 2:
        newevents = this.state.dividedevents.future
        break

      case 3:
        newevents = this.state.dividedevents.past
        break

    }
    this.setState({
      eventstorender: newevents,
      option: option
      //eventstorender: this.state.dividedevents[option].data
    })
    return
  }

  render() {
    if (this.state.isFetching){
    return (
      <View style={styles.container}>
      <Text style={styles.loadingtext}> Načítavam vaše obľúbene udalosti </Text>
      <ActivityIndicator size='large' color="#f16522"/>
      </View>
    )
  }else if (this.state.eventstorender.length != 0){
    return (
      <View style={styles.container}>

        <FilterButtons onChange={this._optionChanged.bind(this)}/>
        <FlatList style={styles.flatliststyle}
        data={this.state.eventstorender}
        renderItem={this._flatListRenderer.bind(this)}
        keyExtractor={(item,index) => item._id}
        />
      </View>
    )
  }else{
    var textday = "Bohužiaľ, dnes sa v okolí Prešova nekonajú žiadne podujatia o ktorých vieme."
    var textweek = "Bohužiaľ, tento týždeň sa v okolí Prešova nekonajú žiadne podujatia o ktorých vieme."
    var textnext = "Bohužiaľ, nevieme o žiadných ďalších udalostiach v budúcnosti."
    var textpast = "Minulé eventy sa už nedajú užiť tak naplno ako tie budúce."
    var rendertext = ""
    switch (this.state.option)
    {
      // Today
      case 0:
        rendertext = textday
        break

      case 1:
        rendertext = textweek
        break

      case 2:
        rendertext = textnext
        break

      case 3:
        rendertext = textpast
        break

    }
    return (
      <View style={styles.container}>

        <FilterButtons onChange={this._optionChanged.bind(this)}/>
        <Text style={styles.zial}>
          {rendertext}
        </Text>
      </View>
      )
    }
  }
}

