import React, { Component } from 'react';
import { View, StyleSheet, Text, ScrollView, Dimensions, TouchableOpacity, Image, ImageBackground } from 'react-native';
import { Router, Switch, Route, Link } from './routing/routing';
import {withRouter} from 'react-router'

import HomeScreen from "./screens/HomeScreen.js"
import SearchScreen from "./screens/SearchScreen.js"
import SavedEventsScreen from "./screens/SavedEventsScreen.js"
import SearchResultsScreen from "./screens/SearchResultsScreen.js"
import AdminScreen from "./screens/AdminScreen.js"
import DetailsScreen from "./screens/DetailsScreen.js"
import MoreScreen from "./screens/MoreScreen.js"
import Screen404 from "./screens/Screen404.js"
import MyTabBar from "./components/MyTabBar.js"

import Icon from 'react-native-vector-icons/Ionicons'


//TODO Not using StyleSheet but just object
// In case of performance issues, this might be the problem
var styles = {
  outercontainer:{
    flex:1,
    flexDirection: 'row',
    justifyContent: 'center',
    //backgroundColor: 'rgba(241, 101, 34, 0.6)'
    backgroundColor: "#EEE"
  },
  container:{
    width: Dimensions.get("window").width,
    height: Dimensions.get("window").height,
    backgroundColor: "#FFF",
  },
  header:{
    borderBottomColor: '#f16522',
    borderBottomWidth: 4,
    borderTopLeftRadius: 20,
    borderTopRightRadius: 20,
    elevation: 10,
    height: '60px',
    justifyContent: 'center',
    backgroundColor: "#FFF",
    alignItems: 'center',
  },
  headerTitle:{
    textAlign:"center",
    fontSize: '25px',
    fontWeight: 'bold',
    color: "#555"
  },
  headerImage:{
    width: "200px",
    height: '35px',
    resizeMode: 'contain'
  },
  bottomTabBar:{
    height: '50px',
    flexDirection:'row',
    justifyContent: 'space-around',
    alignItems: 'center',
    backgroundColor: "#FFF",
    borderBottomLeftRadius: 20,
    borderBottomRightRadius: 20
  },
  button: {
    flex: 1,
    alignItems: 'center'
  },
  innerrouter:{
    flex:1,
    justifyContent: 'flex-start'
  },
}

export default class AppStart extends Component
{

  constructor(props){
    super(props)
    this.onDimensionChange = this.onDimensionChange.bind(this)
    this.setProperDimensions({window: Dimensions.get("window")})
  }

  setProperDimensions(dims){
    console.log("changed")
    console.log(dims)
    styles.container.height = dims.window.height
    if (dims.window.width > dims.window.height){
      // In case landscape, change width and add border and shadows
      console.log("landscape")
      var newstyle = {
        width : dims.window.height * 1.5,
        height: Dimensions.get("window").height,
        borderColor : "#f16522",
        borderWidth : 1,
        borderRadius : 20,
        backgroundColor: "#FFF",

        // SHADOW
        shadowColor: "#000",
        shadowOffset: {
        	width: 0,
        	height: 12,
        },
        shadowOpacity: 0.58,
        shadowRadius: 16.00,

        elevation: 24,
      }
      styles.container = newstyle
    }
  }

  onDimensionChange(dims){
    this.setProperDimensions(dims)
    this.forceUpdate()
  }

  componentWillMount() {
    Dimensions.addEventListener("change", this.onDimensionChange);
  }

  componentWillUnmount() {
    // Important to stop updating state after unmount
    Dimensions.removeEventListener("change", this.onDimensionChange);
  }

  render(){
    console.log(styles.container)
    return(
      <View source={require("./assets/logores.png")} style={styles.outercontainer}
      resizeMode="repeat">
        <View style={styles.container}>
          <Router style={styles.innerrouter}>
            <View style={styles.header}>
              <Image source={require("./assets/logores.png")} style={styles.headerImage}/>
              </View>
              <Switch>
                <Route exact path="/" render={props => <HomeScreen {...props}/>} />
                <Route path="/search" render={props => <SearchScreen {...props}/>} />
                <Route path="/savedevents" render={props => <SavedEventsScreen {...props}/>} />
                <Route path="/appinfo" render={props => <MoreScreen {...props}/>} />
                <Route path="/details/:id" render={props => <DetailsScreen {...props}/>} />
                <Route path="/searchresults/" render={props => <SearchResultsScreen {...props}/>} />
                <Route path="/admin" render={props => <AdminScreen {...props}/>} />
                <Route render={props => <Screen404 {...props}/>} />
              </Switch>

              <View style={styles.bottomTabBar}>
                <HomeButton/>
                <SearchButton/>
                <HeartButton style={styles.button}/>
                <MoreButton style={styles.button}/>
              </View>

              </Router>
              </View>
      </View>
    )}
}

const btn = {
active: '#FF9A46',
inactive: '#AAA'
}

const MoreButton = withRouter(({ history, location}) => {
  var route = "/appinfo"
  return(<TouchableOpacity style={styles.button} onPress={() => {history.push(route)}}>
    <Icon name='md-more' type="ionicon" size={30} color={route == location.pathname ? btn.active : btn.inactive} />
  </TouchableOpacity>)
  }
)

