import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  Image,
  TouchableOpacity,
  TextInput,
  FlatList
} from 'react-native';

import { SearchBar } from 'react-native-elements';
import Icon from 'react-native-vector-icons/Ionicons'
import {withRouter} from 'react-router'

const hangouterColor = '#FF9A46';

const filters = [
  /*{
    id: 'popular',
    title: 'Populárne',
  },
  {
    id: 'today',
    title: 'Dnes',
  },*/
  {
    id: 'art',
    title: 'Umenie',
  },
  {
    id: 'theatre',
    title: 'Divadlo',
  },
  {
    id: 'music',
    title: 'Hudba',
  },
  {
    id: 'party',
    title: 'Párty',
  },
  {
    id: 'movie',
    title: 'Kino',
  },
  {
    id: 'education',
    title: 'Vzdelanie',
  },
];

export default class SearchScreen extends Component {
  constructor(props){
    super(props)
    this.state = {
      search: '',
      filterList: filters
    }
  }
  updateSearch = search => {
     this.setState({ search });
     this.forceUpdate()
  };

  _onElementPress(item, history)
  {
    if (item.id === "popular" || item.id === "today"){
      history.push("/searchresults/" + "?condition=" + item.id )
    } else {
      history.push("/searchresults/" + "?category=" + item.id )
    }
  }

  _onSearchRequest(search, history)
  {
    console.log(search)
    history.push("/searchresults/" + "?searchterm=" + search)
  }

  _flatListRenderer({item}){
    var Element = withRouter(({history}) => (
      <View style={styles.listelement}>
        <TouchableOpacity style={styles.titleblock} onPress={() => {this._onElementPress(item, history)}}>
        <Text style={styles.title}> {item.title} </Text>
        </TouchableOpacity>
        <TouchableOpacity style={styles.heartblock}>
          <Icon name="ios-heart" size={37} color={this.state.favourite ? '#F00': '#AAA'} />
        </TouchableOpacity>
      </View>
    ))
    return (<Element/>)
  }

  render() {
    const { search } = this.state;

    return (
      <View style={styles.container}>
        <SearchBar
        inputStyle={{backgroundColor: 'white', fontSize:17}}
        containerStyle={{backgroundColor: 'white', padding: 10, borderWidth: 1.1, borderRadius: 10, margin: 5, borderColor: '#333'}}
        searchIcon={{name:'md-search', type:"ionicon",size:30}}
        clearIcon={{name:'md-close', type:"ionicon",size:30}}
        cancelIcon={{name:'md-arrow-back', type:"ionicon",size:30}}
        placeholderTextColor={hangouterColor}
        placeholder=''
        platform="android"
        onChangeText={this.updateSearch}
        value={search}
        onBlur={() => {this._onSearchRequest(this.state.search, this.props.history)}}/>

        <FlatList style={styles.flatliststyle}
        data={this.state.filterList}
        renderItem={this._flatListRenderer.bind(this)}
        keyExtractor={(item,index) => item.id}
        />
      </View>

    )
  }
}

const styles = StyleSheet.create({
  container:{
    flex: 1,
    margin: 10
  },
  title:{
    fontSize: 20,
  },
  listelement:{
    flex: 1,
    flexDirection: 'row',
    margin: 5,
    borderColor: '#FFF',
    borderWidth: 5,
    borderRadius: 10,
    justifyContent: 'space-between',
    alignItems: 'center',
    shadowOffset:{width:0, height:5,},
    shadowColor: '#CCC',
    shadowOpacity: 0.5,
    shadowRadius: 15,
    elevation: 22,
  },
  titleblock: {
    flex: 1,
    paddingHorizontal:10,
    paddingVertical: 10,
  },
  heartblock: {
    paddingHorizontal: 20,
    paddingVertical: 10,
  }
})
